.section-3 {
    height: 100%;
    width: 100%;
    position: relative;
    // background-image: url(./../../img/zabor-3.png);
    // background-repeat:no-repeat;
    // background-position: left;
    // background-size: cover;
    z-index: 2;


    &-img{
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &-container {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    &.fadeUppText{
        .section-3{
            &-wrapper{
                opacity: 1;
                transform: translateY(0) !important;
            }
        }
    }
    &-wrapper{
        // margin-bottom: 165px;
        padding-top: 65px;
        opacity: 0;
        transition: 0.7s ease-in-out;
        transform: translateY(50px);
    }
    &__title{
        text-align: center;
        color: #fff;
        font-size:  var(--fz-xl);
        &::after{
            content: '';
            position: absolute;
            top: -55px;
            left: 50%;
            transform: translateX(-50%);
            border: 1px solid var(--c-primary);
            width: 80px;
        }
    }
    &__items{
        display: flex;
        column-gap: 80px;
        margin-bottom: 100px;
    }
    &__item{
        animation: slideInUp;
        animation-duration: 2s;
        animation-delay: 0.5s ;
        p{
            font-size:  var(--fz-s);
            margin-bottom: 25px;
        }
        &-title {
            font-size:  var(--fz-l) !important;
            font-weight: 700 !important;
            color: var(--c-primary) !important;
            margin-bottom: 35px !important;
        }
    
    }
    &__btn{
        color: #fff;
        font-size:  var(--fz-s);
        font-weight: 700;
        display: block;
        width: 300px;
        text-align: center;
        margin: 0 auto;
        background: var(--c-secondary);
        padding: 28px 0;

        span {
            position: relative;
        }
    }
}


